import React from 'react';
import { makeStyles, Typography } from '@material-ui/core';
import design from '../images/design.png'
import ecommerce from '../images/ecommerce.png'
import hosting from '../images/hosting.png'
import tm from '../images/tm.png'
import mobile from '../images/mobile.png'
import { Fade } from 'react-reveal/';


const useStyles = makeStyles((theme) => ({
    container: {
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        // justifyContent:'center'
    },
    toolbar: {
        height: '150px',
        // background: 'blue'
    },
    containerServicios: {
        display: 'flex',
        justifyContent: 'space-around',
        width: '100%',
        // background: 'blue',
        flexWrap: 'wrap',


    },
    servicio: {
        background: 'white',
        width: '90%',
        marginTop: '40px',
        // background: 'blue',
        [theme.breakpoints.up('sm')] : {
            // display: 'none',
            width: '46%'
        },
        [theme.breakpoints.up('lg')] : {
            // display: 'none',
            width: '31%',
        }
    },
    foto: {
        width: '30vw',
        height: '30vw',
        maxWidth: '120px',
        maxHeight: '120px'
    },
    containerLista: {
        display: 'flex',
        alignItems: 'center',
        padding: '10px',
        boxShadow: '2px 3px 6px 3px rgba(0, 0, 0, 0.2)',
        borderRadius: '7px',
        marginTop: '20px',
        webkitTransition: 'padding 2s',
        transform: 'scale(1)',
        transition: 'all .5s',
        '&:hover': {
            transform: 'scale(1.07)',
            boxShadow: '5px 5px 10px 8px rgba(0, 0, 0, 0.2)',
            transition: 'all .5s'
        }


    }
}))

const objServices = [ 
    {
        servicio: 'Diseño web',
        foto: design,
        texto: 'Nuestros diseños creativos harán que su página web sea el portafolio de presentación de su empresa. Capturamos la esencia de su marca para transmitirla a su público.'
    },
    {
        servicio: 'Tiendas virtuales',
        foto: ecommerce,
        texto: 'Autoadministrables y de fácil manejo, incluimos un carrito de compras para tu negocio online y distintos medios de pago. Se uno de los grandes y véndele al mundo.'
    },
    {
        servicio: 'Hosting',
        foto: hosting,
        texto: 'Nuestros planes de Hosting cuentan con todo lo necesario para publicar su página web y administrar su s cuentas de correo corporativas.'
    },
    {
        servicio: 'Registro de marca',
        foto: tm,
        texto: 'Desarrollo, diseño y gestión de su marca.'
    },
    {
        servicio: 'Aplicaciones móviles',
        foto: mobile,
        texto: '¿Tenes en mente una app para tu empresa? Te ayudamos a desarrollar la aplicación movil que potenciara al máximo tu marca.'
    },
    
]

const Services = () => {
    const classes = useStyles();

    const listarServicios = objServices.map((service, index) => {
        return(
            <Fade left>
            <div className={classes.servicio} key={index}>
                <Typography variant="h5">{service.servicio}</Typography>
                <div className={classes.containerLista}>
                    <img src={service.foto} alt="foto de servicio" className={classes.foto}/>
                    <Typography>{service.texto}</Typography>
                    
                </div>
            </div>
            </Fade>
        )
    })

    return ( 
        <div className={classes.container}>
            <div className={classes.toolbar} id="services"></div>
            <Typography variant="h4">Servicios</Typography>
            <div className={classes.containerServicios}>
                {listarServicios}
            </div>
        </div>
    );
}
 
export default Services;