import React from 'react';
import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles(() => ({
    container: {
        position: 'fixed',
        bottom: 0,
        right: 0,
        display: 'flex',
        flexDirection: 'column',
        height: '130px',
        justifyContent: 'space-between',
        margin: '20px'
    },
    iconoFb: {
        display: 'flex',
        alignItems: 'center', 
        justifyContent: 'center',
        background: 'white',
        transition: 'background .5s',
        borderRadius: '50%',
        width: '57px',
        height: '57px',
        boxShadow: '2px 3px 6px 3px rgba(0, 0, 0, 0.4)',
        '&:hover': {
            background: 'rgb(73, 73, 182)',
            transition: 'background .5s',
        }
    },
    iconoWsp: {
        display: 'flex',
        alignItems: 'center', 
        justifyContent: 'center',
        transition: 'background .5s',
        borderRadius: '50%',
        color: 'green',
        width: '51px',
        height: '51px',
        boxShadow: '2px 3px 6px 3px rgba(0, 0, 0, 0.5)',
        '&:hover': {
            background: 'green',
        
        }
    }
}))

const FbWp = () => {
    const classes = useStyles();
    return ( 
        <div className={classes.container}>
            <div className={classes.iconoFb}><i className="fab fa-facebook icono iconofacebook"></i></div>
            <div className={classes.iconoWsp}><i className="fab fa-whatsapp icono iconoWsp"></i></div>
        </div>

    );
}
 
export default FbWp;