import React from 'react';
import { makeStyles, Typography } from '@material-ui/core';
import logo from '../images/logo.png'

import FacebookIcon from '@material-ui/icons/Facebook';
import MailIcon from '@material-ui/icons/Mail';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';

const useStyles = makeStyles((theme) => ({
    containerFooter: {
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
        flexDirection: 'column',
        width: '100%',
        minHeight: '400px',
        marginTop: '100px',
        background: '#3d3d3d',
        color: 'white',
        padding: '20px 0 20px 0'
    },
    logo: {
        width: '150px',
        height: '105px'
    },
    links: {
        textDecoration: 'none',
        color: 'white',
        transition: 'all .5s',
        '&:hover': {
            color: '#d03333',
            transition: 'all .5s',
        }
    },
    containerContacto: {
        display: 'flex',
        // background: 'blue',
        width: '80%',
        justifyContent: 'space-around',
        flexDirection: 'column',
        alignItems: 'center',
        [theme.breakpoints.up('md')] : {
            // display: 'none',
            flexDirection: 'row'
        }
    },
    containerLinks: {
        // background: 'blue',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        justifyContent: 'flex-start',
        // marginTop: '-40px',
        // background: 'red',
        height: 'auto',
        marginBottom: '40px',
        [theme.breakpoints.up('md')] : {
            // display: 'none',
            height: '330px',
        }
    },
    centerLinks: {
        display: 'flex',
        justifyContent: 'center',
        marginTop: '20px',
        alignItems: 'center',
                color: 'white',
        transition: 'all .5s',
        '&:hover': {
            color: '#d03333',
            transition: 'all .5s',
        }
    },
    containerLogo: {
        // background: 'red',
        width: '90%',
        height: '120px',
        borderBottom: '2px solid white',
        marginBottom: '40px'
    },
    containerMapa: {
        width: '100%',
        // background: 'red',
        height: '340px',
        [theme.breakpoints.up('md')] : {
            // display: 'none',
            width: '50%',
        }
    },
    mapa: {
        width: '100%',
        maxWidth: '500px',
        height: 300,
        frameborder: 0,
        scrolling: 'none',
        marginheight: 0,
        marginwidth: 0,
    },
    titulo: {
        textAlign: 'center', 
        // background: 'red',
        width: '100%'
    }
}))


const Footer = () => {
    const classes = useStyles();
    const mapa = <iframe className={classes.mapa} title="a" src="https://maps.google.com/maps?width=100%25&amp;height=300&amp;hl=es&amp;q=Calle%2049%20n%C2%B0%20876%20la%20plata+(Consultora%20YC)&amp;t=&amp;z=14&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"></iframe>
    return (
        <footer className={classes.containerFooter}>
            <div className={classes.containerLogo}><img src={logo} alt="logo Consultora YC" className={classes.logo}/></div>
            <div className={classes.containerContacto}> 
                <div className={classes.containerLinks}>
                    <div className={classes.titulo}>
                        <Typography variant="h4">Contáctanos</Typography>
                    </div>
                    
                    <div className={classes.centerLinks}><WhatsAppIcon /><a href="https://api.whatsapp.com/send?phone=541133003138" target="_blank" className={classes.links}><Typography variant="h6">  11 3300-3138</Typography></a></div>
                    <div className={classes.centerLinks}><MailIcon /><a href="mailto:ConsultGlobalYC@gmail.com" className={classes.links}><Typography variant="h6">  ConsultGlobalYC@gmail.com</Typography></a></div>
                    <div className={classes.centerLinks}><FacebookIcon/><a href="https://www.facebook.com/gestoriaprevisionalyc" target="_blank" className={classes.links}><Typography variant="h6">  Gestoria Previcional YC</Typography></a></div>
                </div>
                <div className={classes.containerMapa}>
                    {/* <Typography variant="h5">La Plata</Typography>
                    <Typography variant="h6">Calle 49 n° 876 1/2 Piso 1 Oficina 10</Typography> */}
                    {mapa}
                </div>
            </div>
        </footer> 
    );
}
 
export default Footer;