import React, { useState } from 'react';
import logo from '../images/logo.png';
import { makeStyles } from '@material-ui/core'
import Drawer from './Drawer'
import { Bounce } from 'react-reveal/';


const useStyles = makeStyles((theme) => ({
    logo: {
        width: '100px',
        height: '65px',
        [theme.breakpoints.down('sm')] : {
            // display: 'none',
            width: '70px',
            height: '45px'
        }
    },
    header: {
        display: 'flex',
        position: 'fixed',
        justifyContent: 'space-around',
        zIndex: 2,
        width: '100%',
        height: '100px',
        [theme.breakpoints.down('sm')] : {
            // display: 'none',
            height: '80px',
        }

        // top: 0,
        // left: 0
    },
    headerActive: {
        display: 'flex',
        position: 'fixed',
        justifyContent: 'space-around',
        zIndex: 2,
        width: '100%',
        height: '100px',
        background: 'black',
        transition: 'all .5s linear .5s',
        [theme.breakpoints.down('sm')] : {
            // display: 'none',
            height: '80px',
        }
    },
    container: {
        display: 'flex',
        justifyContent: 'space-between',
        width: 'auto',
        padding: '18px',
        // background: 'blue',
        alignItems: 'center',
        [theme.breakpoints.down('sm')] : {
            // display: 'none',
            width: '80%',
        }
    },
    nav: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-around',
        width: '50%',
        [theme.breakpoints.down('sm')] : {
            // display: 'none',
            display: 'none'
        }
        
    },
    urls: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: 'white',
        textDecoration: 'none',
        fontSize: '20px',
        height: '70%',
        borderBottom: '4px solid transparent',
        transition: 'all .1s linear .2s',
        // background: 'red',
        width: '120px',
        '&:hover': {
            borderBottom: '4px solid red',
            fontWeight: '600',
            transition: 'all .1s linear .2s',
        }
    }
}))

const urls = [
    {
        url: '#',
        direccion: 'Home'
    },
    {
        url: '#',
        direccion: 'Servicios'
    },
    {
        url: '#',
        direccion: 'Novedades'
    },
    {
        url: '#',
        direccion: 'Contáctenos'
    },
]


const Header = () => {
    const classes = useStyles();
    const [navbar, setNavbar] = useState(false);

    const changeBackground = () => {
        if(window.scrollY >= 200){
            setNavbar(true);
        } else {
            setNavbar(false);
        }
    }

    window.addEventListener('scroll', changeBackground)    

    const listarURL = urls.map((url, index) => {
        return(
            <a href={url.url} key={index} className={classes.urls}>{url.direccion}</a>
        )
    })


    return ( 
        <header className={(navbar) ? classes.headerActive : classes.header}>
            <div className={classes.container}>
                <Bounce left><img src={logo} alt="logo" className={classes.logo}/></Bounce>
                
                <Bounce right><Drawer className={classes.drawer}/></Bounce>   
            </div>
            <Bounce top>
            <div className={classes.nav}>
                {listarURL}
            </div>
            </Bounce>
        </header>

    );
}
 
export default Header;