import React from 'react';
import transformaciondigital from '../images/transformaciondigital.mp4';
import { makeStyles, Typography } from '@material-ui/core';
import { Fade } from 'react-reveal/';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import transitions from '@material-ui/core/styles/transitions';

const useStyles = makeStyles(() => ({
    video: {
        width: '100%',
        height: '100vh',
        objectFit: 'cover',
        zIndex: 0,
    },
    container: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        zIndex: 1,
    },
    texto: {
        color: 'white'
    },
    containerTexto: {
        display: 'flex',
        flexDirection: 'column',
        position: 'absolute'
    },
    arrow: {
        position: 'absolute',
        left: '47%',
        bottom: 60,
        transition: 'all .2s',
        color: 'white',
        fontSize: '40px',
        animation: 'MoveUpDown 1s linear infinite',
        '&:hover': {
            transform: 'translateY(5px)',
            transition: 'transform .2s'
        }
    }
}))

const Background = () => {
    const classes = useStyles();

    return ( 
        <div className={classes.container}>
            <video autoPlay="autoplay" loop="loop" muted src={transformaciondigital} className={classes.video} >
            </video>
            <div className={classes.containerTexto}>
                <Fade left>
                    <div className={classes.containerTransformacion}>
                        <Typography className={classes.texto} variant="h3">      
                            Transformación Digital
                        </Typography>
                    </div>
                </Fade>
            </div>
            <a href="#services"><ArrowDownwardIcon className='object' className={classes.arrow}/></a>

        </div>
    );
}
 
export default Background;